import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { FIREBASE_CONFIG } from "./app.firebase.config";
import { AngularFireModule } from 'angularfire2';
import { AngularFireAuthModule }from 'angularfire2/auth';
import { AngularFireDatabaseModule } from 'angularfire2/database';

import { MyApp } from './app.component';
import { AuthProvider } from '../providers/auth/auth';
import { Facebook } from '@ionic-native/facebook'
import { StreamingMedia } from '@ionic-native/streaming-media';

import { HomePage } from '../pages/home/home';
import { LoginPage } from '../pages/login/login';
import { DuhovniKutakPage } from '../pages/duhovni-kutak/duhovni-kutak';
import { DogadjanjaPage } from '../pages/dogadjanja/dogadjanja';

import { LaunchNavigator } from '@ionic-native/launch-navigator';
import { Geolocation } from '@ionic-native/geolocation';
import { ConnectivityServiceProvider } from '../providers/connectivity-service/connectivity-service';
import { Network } from '@ionic-native/network';
import { GoogleMaps } from '@ionic-native/google-maps';
import { Camera } from '@ionic-native/camera';
import { Firebase } from '@ionic-native/firebase';
import { LocalNotifications } from '@ionic-native/local-notifications';
import { Diagnostic } from '@ionic-native/diagnostic';

import { TimelineComponent } from '../components/timeline/timeline';
import { TimelineTimeComponent } from '../components/timeline/timeline';
import { TimelineItemComponent } from '../components/timeline/timeline';
import { DatePipe } from '@angular/common';


@NgModule({
  declarations: [
    MyApp,
    HomePage,
    LoginPage,
    DuhovniKutakPage,
    DogadjanjaPage,
    TimelineComponent,
    TimelineItemComponent,
    TimelineTimeComponent

  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/atrakcije-detalji/atrakcije-detalji.module.ngfactory#AtrakcijeDetaljiPageModuleNgFactory', name: 'AtrakcijeDetaljiPage', segment: 'atrakcije-detalji', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/atrakcije/atrakcije.module.ngfactory#AtrakcijePageModuleNgFactory', name: 'AtrakcijePage', segment: 'atrakcije', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/galerija/galerija.module.ngfactory#GalerijaPageModuleNgFactory', name: 'GalerijaPage', segment: 'galerija', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/gastro-detalji/gastro-detalji.module.ngfactory#GastroDetaljiPageModuleNgFactory', name: 'GastroDetaljiPage', segment: 'gastro-detalji', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/gastro/gastro.module.ngfactory#GastroPageModuleNgFactory', name: 'GastroPage', segment: 'gastro', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/interaktivna-mapa/interaktivna-mapa.module.ngfactory#InteraktivnaMapaPageModuleNgFactory', name: 'InteraktivnaMapaPage', segment: 'interaktivna-mapa', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/komunalno/komunalno.module.ngfactory#KomunalnoPageModuleNgFactory', name: 'KomunalnoPage', segment: 'komunalno', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/register/register.module.ngfactory#RegisterPageModuleNgFactory', name: 'RegisterPage', segment: 'register', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/reset-password/reset-password.module.ngfactory#ResetPasswordPageModuleNgFactory', name: 'ResetPasswordPage', segment: 'reset-password', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/smjestaj-detalji/smjestaj-detalji.module.ngfactory#SmjestajDetaljiPageModuleNgFactory', name: 'SmjestajDetaljiPage', segment: 'smjestaj-detalji', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/smjestaj/smjestaj.module.ngfactory#SmjestajPageModuleNgFactory', name: 'SmjestajPage', segment: 'smjestaj', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/user-postavke/user-postavke.module.ngfactory#UserPostavkePageModuleNgFactory', name: 'UserPostavkePage', segment: 'user-postavke', priority: 'low', defaultHistory: [] }
  ]
}),
    AngularFireModule.initializeApp(FIREBASE_CONFIG),
    AngularFireAuthModule,
    AngularFireDatabaseModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    LoginPage,
    DuhovniKutakPage,
    DogadjanjaPage,
    TimelineComponent,
    TimelineItemComponent,
    TimelineTimeComponent

  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    AuthProvider,
    Facebook,
    StreamingMedia,
    LaunchNavigator,
    Geolocation,
    ConnectivityServiceProvider,
    Network,
    GoogleMaps,
    Camera,
    Firebase,
    LocalNotifications,
    DatePipe,
    Diagnostic

  ]
})
export class AppModule {}



